import React, { Component } from 'react'
import { Table, Divider, Button, message } from 'antd'
import { goodstypeattributelist, deletegoodstypeattribute } from '../../api/goods'
import datefilter from '../../tool/datefilter'
import { typeattributeilter } from '../../tool/statusfilter'
import { Link } from 'react-router-dom'
import getUrlParam from '../../tool/getUrlParam'

const { Column } = Table
class role extends Component {
	constructor(props) {
		super(props)
		this.state = {
			data: []
		}
	}
	getlist() {
		goodstypeattributelist({
			id: getUrlParam('id')
		})
			.then((res) => {
				if (res.status === 1) {
					this.setState({
						data: res.data
					})
				}
			})
			.catch((err) => {
				console.log('err: ', err)
			})
	}
	componentDidMount() {
		this.getlist()
	}
	deletegoodstypeattribute(id) {
		deletegoodstypeattribute({
			id
		})
			.then((res) => {
				console.log('res: ', res)
				this.getlist()
				message.success(res.message)
			})
			.catch((err) => {
				console.log('err: ', err)
			})
	}
	render() {
		return (
			<div>
				<Link to={`/addgoodstypeattribute?id=${getUrlParam('id')}&type=0`}>
					<Button type="primary"></Button>
				</Link>
				<Table dataSource={this.state.data}>
					<Column
						title=""
						dataIndex="parent"
						key="cate_id"
						render={(text, record) => <div type="primary">{text[0].title}</div>}
					/>
					<Column title="" dataIndex="title" key="title" />
					<Column
						title=""
						dataIndex="attr_type"
						key="attr_type"
						render={(text, record) => <div type="primary">{typeattributeilter(text)}</div>}
					/>
					<Column title="" dataIndex="attr_value" key="attr_value" />
					<Column
						title=""
						key="add_time"
						dataIndex="add_time"
						render={(text, record) => <div type="primary">{datefilter(text)}</div>}
					/>
					<Column
						title=""
						key="_id"
						dataIndex="_id"
						render={(text, record) => (
							<span>
								<Link to={`/addgoodstypeattribute?id=${text}&type=1`}>
									<Button type="primary"></Button>
								</Link>
								<Divider type="vertical" />
								<Button type="danger" onClick={this.deletegoodstypeattribute.bind(this, text)}>
									
								</Button>
							</span>
						)}
					/>
				</Table>
			</div>
		)
	}
}

export default role
